var daysTable = {
    0: 'ראשון',
    1: 'שני',
    2: 'שלישי',
    3: 'רביעי',
    4: 'חמישי',
    5: 'שישי',
    6: 'שבת'
};

angular.module('RouteExplorer').filter('week_day1', function () {
    return function (day) {
        if (day == 'all') {
            return 'כל הימים';
        }
        return daysTable[day - 1] || '??? ' + day;
    };
}).filter('week_day0', function () {
    return function (day) {
        if (day == 'all') {
            return 'כל הימים';
        }
        return daysTable[day] || '??? ' + day;
    };
}).filter('hours', function () {
    return function (hours) {
        var fix = function fix(h) {
            return h >= 24 ? h % 24 : h;
        };
        if (hours == 'all') {
            return 'כל היום';
        }
        var h1 = fix(hours[1]);
        var h0 = fix(hours[0]);
        return h1 + ' - ' + h0;
    };
}).filter('month_name', function () {
    var months = ['ינואר', 'פברואר', 'מרץ', 'אפריל', 'מאי', 'יוני', 'יולי', 'אוגוסט', 'ספטמבר', 'אוקטובר', 'נובמבר', 'דצמבר'];
    return function (m) {
        return months[m - 1];
    };
});
'use strict';

angular.module('RouteExplorer').filter('duration', function () {
    return function (seconds) {
        var negative = false;
        seconds = Math.trunc(seconds);
        if (seconds < 0) {
            negative = true;
            seconds = -seconds;
        }

        var minutes = Math.trunc(seconds / 60);
        seconds -= minutes * 60;
        var hours = Math.trunc(minutes / 60);
        minutes -= hours * 60;

        if (seconds < 10) seconds = '0' + seconds;
        if (minutes < 10 && hours !== 0) minutes = '0' + minutes;

        var res = minutes + ':' + seconds;
        if (hours !== 0) res = hours + ':' + res;

        if (negative) res = '-' + res;

        return res;
    };
});
